.overlay{

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;


}

.container{

position: relative;
top: 10%;
margin: auto;
height: 500px;
width: 500px;
background-color: white;
padding: 50px;
z-index: 1000;
-webkit-animation: myAnim 1s ease 0s 1 normal forwards;
        animation: myAnim 1s ease 0s 1 normal forwards;
}

@-webkit-keyframes myAnim {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes myAnim {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
